body {
  margin: 0;
  padding: 0;
  font-family: 'Dosis', sans-serif;
  height: 100vh;
}
.dashboard-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  width: 100vw;
  padding-bottom: 50px;
}

.add-content {
  width: 100vw;
}

.add-content-wrapper {
